export const menu = (obj, index, bool) => {
    obj.forEach(element => {
        element.isChanged = false;
    });

    if (!bool) {
        obj[index].isChanged = true;
    }

    return obj;
};

export const menuItems = [
    {
        title: "Գլխավոր",
        link: "/SmartCode/"
    }, {
        title: "Դասընթացներ",
        link: "/SmartCode/courses"
    }, {
        title: "Բլոգ",
        link: "/SmartCode/blog"
    }, {
        title: "Մեր մասին",
        link: "/SmartCode/about"
    }
];

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    menu,
    menuItems : menuItems
};