// props.show); //  }}>{props.titleinfo} {props.show}         </a>     </li> );
// export default // NewInfo; ? <p>{props.text}</p> : null

const NewInfo = props => (

    <li className={`list-info-li ${props.show
        ? 'show'
        : ''}`}>
        <img src={props.icon} alt="IconList"/>
        <Accordion className="show-section-list">

            <AccordionSummary
                expandIcon={< ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header">{props.titleinfo}
            </AccordionSummary>
            <AccordionDetails>
                <p>
                    {props.text}.
                </p>
            </AccordionDetails>
        </Accordion>
    </li>

)

export default NewInfo;
